/* 全体 */
.App {
  user-select: none;
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* スライド */
.App-slide {
  position: absolute;
  width: 140vmin;
  height: 80vmin;
  z-index: 0;
}

/* 選択肢ボタン */
.App-select-button {
  font-size: 5vmin;
  font-family: sans-serif;
  text-shadow: -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000;
  color: white;

  z-index: 10;

  -webkit-transform: skew(-10deg);
  transform: skew(-10deg);
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
  border-radius: 1vmin;
  border: 1px solid #333;
  background-color: #911;

  width: 50vmin;
  height: 7vmin;
  margin: 1.5vmin auto;
}

.App-select-button:hover {
  text-shadow: -3px 3px 3px #000, -3px 3px 3px #000, -3px 3px 3px #000, -3px 3px 3px #000;
  box-shadow: 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3, 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3, 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3, 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3, 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3, 5px 5px 5px #ee3, -5px 5px 5px #ee3, 5px -5px 5px #ee3, -5px -5px 5px #ee3;
  border: 3px solid #333;
  background: repeating-linear-gradient(-45deg,#e22, #e22 5px, #e55 7px, #e55 8px, #e22 10px);
}

.App-select-button-list {
  z-index: 10;
  width: 50vmin;
  margin-top: -15vmin;
}

/* サブボタン */
.App-sub-button {
  font-size: 3.5vmin;
  font-family: sans-serif;
  text-shadow: -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000;
  color: white;

  z-index: 10;

  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
  border-radius: 1vmin;
  border: 1px solid #333;
  background-color: #191;

  width: 20vmin;
  height: 5vmin;
  margin: auto 2vmin;
}

.App-sub-button:hover {
  text-shadow: -2px 2px 2px #000, -2px 2px 2px #000, -2px 2px 2px #000, -2px 2px 2px #000;
  box-shadow: 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3, 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3, 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3, 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3, 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3, 3px 3px 3px #ee3, -3px 3px 3px #ee3, 3px -3px 3px #ee3, -3px -3px 3px #ee3;
  border: 2px solid #333;
  background: repeating-linear-gradient(-45deg,#2e2, #2e2 5px, #5e5 7px, #5e5 8px, #2e2 10px);
}

.App-sub-button-list {
  z-index: 10;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: 30vmin;
  margin-left: 20vmin;
 
  width: 50vmin;
  height: 10vmin;

  display: flex;
}

/* 戻るボタン */
.App-back-icon-back {
  z-index: 10;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40vmin;
  margin-left: -70vmin;
  width: 6vmin;

  border-right: solid 3vmin transparent;
  border-top: solid 5vmin #911;
}
.App-back-icon-back-hover {
  z-index: 10;
  transform: scale(1.1);
  transform-origin: left top;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40vmin;
  margin-left: -70vmin;
  width: 16vmin;

  border-right: solid 3vmin transparent;
  border-top: solid 5vmin #fe2;
}
.App-back-icon-back-border {
  z-index: 5;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40vmin;
  margin-left: -70vmin;
  width: 6.3vmin;

  border-right: solid 3.3vmin transparent;
  border-top: solid 5.4vmin #311;
}

.App-back-icon-button {
  pointer-events : none;
  font-size: 2vmin;
  font-family: sans-serif;
  color: white;

  background-color: transparent;
  border-color: transparent;

  border-right: none;
  border-top: none;

  position: absolute;
  top: 0%;
  left: 0%;
  margin-top: -4.5vmin;
}
.App-back-icon-button-hover {
  pointer-events : none;
  font-size: 2vmin;
  font-family: sans-serif;
  color: black;
  text-shadow: -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000, -1px 1px 1px #000;

  background-color: transparent;
  border-color: transparent;

  border-right: none;
  border-top: none;

  position: absolute;
  top: 0%;
  left: 0%;
  margin-top: -4.5vmin;
  vertical-align: middle;
}

/* ルールに戻るボタン */
.App-rule-back-icon-back {
  z-index: 3;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40vmin;
  margin-left: -65vmin;
  width: 12vmin;

  border-right: solid 3vmin transparent;
  border-top: solid 5vmin #911;
}
.App-rule-back-icon-back:hover {
  border-top: solid 5vmin #c33;
}
.App-rule-back-icon-back-border {
  z-index: 2;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -40vmin;
  margin-left: -65vmin;
  width: 12.3vmin;

  border-right: solid 3.3vmin transparent;
  border-top: solid 5.4vmin #311;
}

.App-rule-back-icon-button {
  transform: scale(1, -1);
  transform-origin: center;
  pointer-events : none;
  font-size: 3vmin;
  font-family: sans-serif;
  color: white;

  background-color: transparent;
  border-color: transparent;

  border-right: none;
  border-top: none;

  position: absolute;
  top: 0%;
  left: 0%;
  margin-top: -5.2vmin;
  margin-left: 6vmin;
}

.App-rule-back-exp {
  pointer-events : none;
  font-size: 1.5vmin;
  z-index: 5;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -39.5vmin;
  margin-left: -52vmin;

  padding: 1vmin 1vmin;
  color: #555;
  background: #eef;
}

.App-rule-back-exp:before {
  z-index: 5;
  content: "";
  position: absolute;
  top: 50%;
  left: -3.5vmin;
  margin-top: -1.5vmin;
  border: 1.5vmin solid transparent;
  border-right: 2vmin solid #eef;
}

/* 共通アイコン */
.App-next-button {
  z-index: 10;
  font-size: 1.2vmin;
  background-color: transparent;
  color: black;
  border: 0px;
  margin-right: 8vmin;
}

.App-next-button > .front {
  z-index: 15;
  position: absolute;
  color: white;
  font-size: 8vmin;
}

.App-next-button > .back {
  position: absolute;
  color: black;
  font-size: 9vmin;
  margin: -0.5vmin -0.5vmin;
}

.App-next-button:hover {
  transform: scale(1.2) translate(-0.7vmin, -0.8vmin);
}

/* チーム */
.App-team {
  z-index: 10;
}

.App-teamline {
  z-index: 10;
  background-color: #fffB;

  display: flex;
  width: 100vmin;
  margin-bottom: 3.5vmin;

  border-radius: 1vmin;
}

.App-team .App-teamline:nth-of-type(1) {
  border-left: solid 7vmin #e22;
}

.App-team .App-teamline:nth-of-type(2) {
  border-left: solid 7vmin #22e;
}

.App-team .App-teamline:nth-of-type(3) {
  border-left: solid 7vmin #2e2;
}

.App-team .App-teamline:nth-of-type(4) {
  border-left: solid 7vmin #ee2;
}

.App-teamline-team-name {
  pointer-events : none;
  font-size: 7vmin;
  font-family: sans-serif;
  color: #000;
  text-shadow: -1px 1px 1px #bbb, 1px 1px 1px #bbb, 1px -1px 1px #bbb, -1px -1px 1px #bbb;

  margin-left: -6vmin;
}

.App-teamline-name {
  pointer-events : none;
  font-size: 3.5vmin;
  font-family: sans-serif;
  color: #000;
  text-shadow: -1px 1px 1px #bbb, 1px 1px 1px #bbb, 1px -1px 1px #bbb, -1px -1px 1px #bbb;

  margin-left: 4vmin;
  margin-top: 2.5vmin;
}

/* バトル */
.App-battle {
  z-index: 10;
  display: flex;
}

.App-battle-header {
  z-index: 10;
  margin-top: -2vmin;
  margin-bottom: 5vmin;
  display: flex;

  width: 100vmin;
  height: 25vmin;
}

.App-teambattle-header {
  z-index: 10;
  margin-top: -2vmin;
  margin-bottom: 5vmin;
  display: flex;

  width: 100vmin;
  height: 15vmin;
}

.App-battle-title {
  z-index: 10;
  background-color: #fffB;
  transform: translateY(3vmin);

  font-size: 7vmin;
  font-family: sans-serif;
  color: #000;

  width: 35vmin;
  height: 20vmin;

  margin-top: -2vmin;
  margin-bottom: 8vmin;
  border-radius: 1vmin;
}

.App-teambattle-title {
  z-index: 10;
  background-color: #fffB;
  transform: translateY(3vmin);

  font-size: 7vmin;
  font-family: sans-serif;
  color: #000;

  width: 100vmin;
  height: 15vmin;
