}

.App-battle-stage-name {
  font-size: 4vmin;
  font-family: sans-serif;
  color: #000;
  margin-top: 1vmin;
}

.App-battle-stage-image {
  width: 30vmin;
  height: 15vmin;
}

.App-battle-stage-image:hover {
