}

.App-battle-name {
  z-index: 10;
  background-color: #fffB;

  display: block;
  width: 25vmin;
  height: 12vmin;
  margin-right: 1.5vmin;
  margin-left: 1.5vmin;
  margin-bottom: 6vmin;

  border-radius: 1vmin;
}

.App-teambattle-name {
  height: 20vmin;
  margin-right: 10vmin;
  margin-left: 10vmin;
}

.App-battle .App-battle-name:nth-of-type(1) {
