}

.App-battle .App-battle-name.App-teambattle-name-1 {
  border-top: solid 7vmin #e22;
}

.App-battle .App-battle-name.App-teambattle-name-2 {
  border-top: solid 7vmin #22e;
}

.App-battle .App-battle-name.App-teambattle-name-3 {
