  border-top: solid 7vmin #ee2;
}

.App-battle-team-name {
  pointer-events : none;
  font-size: 7vmin;
  font-family: sans-serif;
  color: #000;
  text-shadow: -1px 1px 1px #bbb, 1px 1px 1px #bbb, 1px -1px 1px #bbb, -1px -1px 1px #bbb;

  margin-top: -8.5vmin;
}

.App-teambattle-team-name {
  font-size: 5vmin;
  margin-top: -6.5vmin;
}

.App-battle-name-name {
  pointer-events : none;
  font-size: 5vmin;
  font-family: sans-serif;
  color: #000;
  text-shadow: -1px 1px 1px #bbb, 1px 1px 1px #bbb, 1px -1px 1px #bbb, -1px -1px 1px #bbb;

  position: relative;
  top: 40%;
  left: 50%;
