}

/* ステージ選択 */
.App-stage-select {
  z-index: 10;
  background-color: #fffB;
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  justify-content: center;
  width: 130vmin;
  height: 70vmin;
  border-radius: 8vmin;
}

.App-stage-select-stage {
  width: 22vmin;
  height: 11vmin;
  margin-top: 2vmin;
  margin-left: 3vmin;
  margin-right: 3vmin;
}

.App-stage-select-stage:hover {
  transform: scale(1.2) skew(-5deg);
}

/* トーナメント */
.App-tournament-title {
  z-index: 10;
  background-color: #fffB;

  font-size: 7vmin;
